<template>
    <div class="header-home-container">
        <div class="button-header-container">
            <button @click="this.$router.push('/')">Accueil</button>
           <button class="cours">Cours</button>
           <router-link class="header-component" :to="{name: 'admin'}">Cours</router-link>
        </div>
     </div>
</template>
  
<script>
import axios from 'axios';
export default {
    data() {
        return {
            bool: false,
        }
    },
    methods: {
        displayContent: async function() {
            const value = `; ${document.cookie}`;
            let token = value.split(`; session=`);
            if (token.length === 2) {
                token = token.pop().split(';').shift();
            }
            axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}
            try {
                await axios.get('http://localhost:5000/admin', {
                    withCredentials: true
                })
                this.bool = true;
            }
            catch(err) {
                this.bool = false;
            }
        }
    },
    mounted() {
        this.displayContent();
    }
}
</script>
  
<style lang="scss">
.header-home-container {
    width: 100%;
    height: 100%;
    display: flex;
    // justify-content: center;
    align-items: center;
    background-color: #141414;

    .button-header-container {
        height: 100%;
        margin-left: 20%;
        width: 60%;
        display: flex;
        justify-content: center;
        align-items: center;

        button {
            margin-right: 75px;
            font-size: 20px;
            color: white;
            cursor: pointer;
            background: linear-gradient(red, red) bottom / 0 .1rem no-repeat;
            border: none;

            &.cours {
                cursor: auto;
                border-radius: 10%;
                background-size: 80% .1rem;
                color: red;
            }

        }
    }
}
</style>