<template>
    <div class="mod-content-container">
        <div class="add-cours">
            <div class="top-cours-composition">
                <input class="name-cours" type="text" placeholder="Nom" v-model="name">
                <select class="year-selected" name="year-selected" id="year-selected" v-model="year">
                    <option value="0">Date du cours</option>
                    <option value="2023">2023</option>
                    <option value="2022">2022</option>
                    <option value="2021">2021</option>
                    <option value="2020">2020</option>
                    <option value="2019">2019</option>
                </select>
                <select name="type-cours" id="type-cours" v-model="type">
                    <option value="0">Type de cours</option>
                    <option value="Cm">Cm</option>
                    <option value="Td">Td</option>
                    <option value="Fiches">Fiches</option>
                    <option value="Partiels">Partiels</option>
                    <option value="Tp">Tp</option>
                </select>
            </div>
            <div class="bottom-cours-composition">
                <select name="promotion" id="promotion" v-model="promotion">
                    <option value="0">Promotion</option>
                    <option value="SHPI1A">SHPI 1A</option>
                    <option value="SHPI2A">SHPI 2A</option>
                    <option value="MT3A">MT 3A</option>
                    <option value="ME3A">ME 3A</option>
                    <option value="GInd3A">GInd 3A</option>
                    <option value="GM3A">GM 3A</option>
                    <option value="GInfo3A">GInfo 3A</option>
                    <option value="Cyber3A">Cyber 3A</option>
                    <option value="GCB3A">GCB 3A</option>
                </select>
                <select class="name" name="Name" id="name_id" v-model="matiere">
                    <option value="0">Matière</option>
                    <option value="Analyse">Analyse</option>
                    <option value="Algebre">Algèbre</option>
                    <option value="OMP">OMP</option>
                    <option value="Base De Donnees">Base De Données</option>
                    <option value="Electromagnetisme">Electromagnétisme</option>
                    <option value="Analyse numerique">Analyse numérique</option>
                    <option value="Genie logiciel">Génie logiciel</option>
                    <option value="Electrostatique">Electrostatique</option>
                    <option value="Electrocinetique">Electrocinétique</option>
                    <option value="Mecanique">Mécanique</option>
                    <option value="Automatique">Automatique</option>
                    <option value="Architecture">Architecture</option>
                    <option value="Probabilite">Probabilité</option>
                    <option value="Magnetostatique">Magnétostatique</option>
                    <option value="Chime des Materiaux">Chime des Matériaux</option>
                    <option value="Thermodynamique">Thermodynamique</option>
                    <option value="Optique geometrique">Optique géométrique</option>
                    <option value="Optique ondulatoire">Optique ondulatoire</option>
                    <option value="Algo">Algo</option>
                    <option value="Physique des ondes">Physique des ondes</option>
                    <option value="Calcul formel">Calcul formel</option>
                </select>
                <input class="auteur-cours" type="text" placeholder="Auteur" v-model="auteur">
            </div>
            <div class="input-file-cours">
                <input type="file" ref="fileInput">
                <button class="cours-submit" @click="uploadFile">Submit</button>
            </div>
        </div>

    </div>
</template>

<script>
import axios from 'axios';

export default {

    data() {
        return {
            name: '',
            year: '0',
            promotion: '0',
            type: '0',
            path_folder: '',
            matiere: '0',
            auteur: ''
        }
    }, methods: {
        onChangedFile: function (event) {

            var file = event.target.files[0];
            const formData = new FormData();
            formData.append('file', file);
            console.log(formData);
        },
        updateFileName: function (str) {
            return str.replace(/[^a-zA-Z0-9 -]/g, '')
                .replace(/\s/g, '_')
                .replace(/\//g, '');
        },
        uploadFile: async function () {

            const file = this.$refs.fileInput.files[0];

            if (
                this.year == '0' ||
                this.promotion == '0' ||
                this.type == '0' ||
                this.matiere == '0' ||
                this.name == '' ||
                this.auteur == '' ||
                file == undefined
            ) {
                alert('Tous les champs sont obligatoire !')
                return;
            }

            const newDirectory = this.matiere + '/' + this.promotion + '/' + this.type + '/'
            const newName = this.updateFileName(this.name);
            const formData = new FormData();
            formData.append('file', file);
            formData.append('newName', newDirectory + newName);
            formData.append('extension', file.name.split('.').pop())

            try {
                await axios.post('http://localhost:5000/cours', {
                    name: this.name,
                    year: this.year,
                    shpi: this.shpi,
                    type: this.type,
                    path: newDirectory + newName,
                    matiere: this.matiere,
                    auteur: this.auteur
                }).then(() => {
                    alert('Fichier ajouter a la base de donnée !')
                }).catch(() => {
                    alert('Une erreure est survenue, contactez l\'admin')
                })
            } catch (err) {
                alert('Une erreure est survenue, contactez l\'admin')
            }
            try {
                await axios.post('http://localhost:5000/upload', formData).then(() => {
                    alert('Upload du fichier effectué !')
                })
            }catch (err) {
                alert('Une erreure est survenue, contactez l\'admin')
            }
            window.location.reload();

        },
    }

}
</script>

<style lang="scss">
.mod-content-container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;

    .add-cours {
        width: 100%;
        height: 50%;

        .top-cours-composition {

            width: 100%;
            height: 40%;
            display: flex;
            justify-content: center;
            align-items: center;

            .year-selected {
                margin-right: 50px;
                margin-left: 50px;
            }

        }

        .bottom-cours-composition {

            width: 100%;
            height: 40%;
            display: flex;
            justify-content: center;
            align-items: center;

            .name {
                margin-right: 50px;
                margin-left: 50px;
            }
        }

        .input-file-cours {

            width: 100%;
            height: 15%;
            display: flex;
            justify-content: center;
            align-items: center;

            .cours-submit {
                background-color: red;
                border: none;
                border-radius: 15px 15px;
                cursor: pointer;
                height: 50%;
                width: 10%;
                margin-left: 50px;
            }

        }

        .name-cours,
        .auteur-cours {
            outline: none;
            padding-left: 20px;
            height: 5vh;
            width: 20%;
            background-color: white;
            border: none;
            border-radius: 15px 15px;
        }

        select {
            height: 50px;
            width: 10%;
            color: black;
            background-color: white;
            border: none;
            border-radius: 15px 15px;
            padding-left: 20px;
        }

    }
}
</style>